.Button {
  position: fixed;
  bottom: 16px;
  left: 16px;

  border: none;
  border-radius: 4px;
  box-shadow: 0 2px 8px #4442;
  padding-block-start: 8px;
  padding-block-end: 8px;
  padding-inline-start: 16px;
  padding-inline-end: 16px;

  background-color: white;

  font-size: 16px;
  font-weight: 500;

  cursor: pointer;
}
